.volume-slider
{ 
  width: 100%;
}
.volume-slider > input
{ 
  width: 100%;
}
input[type="range"]
{
  appearance: none;
  width: 100%;
  height: 7px;
  background: black;
}
input[type="range"]::-webkit-slider-thumb
{
  appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: blue;
  cursor: pointer;
  box-shadow: 0 0 2px 0 #555;
}
input[type=range]::-webkit-slider-runnable-track
{
  appearance: none;
  box-shadow: none;
  border: none;
  background: transparent;
}