@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');

#drum-machine
{
  width: 80%;
  max-width: 600px;
  background-color: #b3b3b3;
  border: 4px solid orange;
  padding: 30px;
  font-family: 'Russo One', sans-serif;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.pad-bank
{
  width: 60%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
}
.logo
{
  position: absolute;
  right: 30px;
  top: 30px;
  display: flex;
  align-items: center;
}
.logo > i
{
  font-size: 2rem;
}

.controls-container
{
  padding-top: 40px;
  width: 35%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

#display
{
  width: 100%;
  background-color: gray;
  padding: 15px;
  text-align: center;
}


@media screen and (max-width: 768px)
{
  #drum-machine
  {
    padding: 2vw;
  }
  .logo
  {
    right: 2vw;
    top: 2vw;
  }
  .logo > i
  {
    font-size: 1rem;
  }
  .drum-pad
  {
    font-size: 3vw !important;
  }
  .controls-container
  {
    padding-top: 0;
    gap: 1vw;
  }  
  .control > p,
  #display
  {
    font-size: 2.5vw !important;
  }
  #display
  {
    padding: 5px;
  }
}
