.control
{
  text-align: center;
  width: 100%;
}
.select
{
  margin: auto;
  border: 1px solid black;
  width: 30%;
  aspect-ratio: 2 / 1;
  padding: 1px;
  background-color: black;
  display: flex;
}
.inner
{
  width: 50%;
  height: 100%;
  border: 1px solid black;
  cursor: pointer;
}